import { createRouter, createWebHistory } from "vue-router";
import loginStart from '../views/loginFirstView';
import loginEnd from '../views/loginSecondView';
import resetPsw from '../views/resetPassView';
import resetEnd from '../views/resetEndView';

const routes = [
  {
    path: "/",
    name: "signIn",
    component: () => require('../views/authView.vue'),
  },
  {
    path: '/loginStart',
    name: 'loginStart',
    component: loginStart,
  },
  {
    path: '/loginEnd',
    name: 'loginEnd',
    component: loginEnd,
  },
  {
    path: '/resetPassword',
    name: 'resetPassword',
    component: resetPsw
  },
  {
    path: '/resetEnd',
    name: 'resetEnd',
    component: resetEnd
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
